import { useCallback, useState } from 'react';
import useTimeoutEffect from '../core/useTimeoutEffect';
/**
 * a hook to copy text to the clipboard and track its state
 */
const useClipboard = (options = {}) => {
    const { dismiss = 2000 } = options;
    const [text, setText] = useState('');
    const [copied, setCopied] = useState(false);
    const [error, setError] = useState(null);
    const copy = useCallback(async (text) => {
        try {
            await window.navigator.clipboard.writeText(text);
            setText(text);
            setCopied(true);
            setError(null);
        }
        catch (error) {
            setText('');
            setCopied(false);
            setError(error);
        }
    }, []);
    useTimeoutEffect(() => {
        if (dismiss > 0 && copied) {
            setCopied(false);
        }
    }, dismiss, [text, copied]);
    return [copy, text, copied, error];
};
export default useClipboard;
