import { useEffect } from 'react';
/**
 * a hook wrapper for handling async function in `useEffect`
 * @param effect the async function to be invoked
 * @param [destructor] the clean up function you return in `useEffect` effect callback
 * @param [dependencies] the dependency list you pass to `useEffect` as second argument
 *
 * @example
 * useAsyncEffect(
 *   async () => {
 *     await sleep(1000);
 *     return 'done';
 *   },
 *   (result) => {
 *     console.info('unmounted');
 *     console.log(result); // 'done'
 *   }
 *   []
 * );
 */
const useAsyncEffect = (effect, destructor = [], dependencies = []) => {
    let _destructor;
    let _dependencies;
    if (typeof destructor === 'function') {
        _destructor = destructor;
        _dependencies = dependencies;
    }
    else {
        _dependencies = destructor;
    }
    return useEffect(() => {
        let result;
        (async () => {
            result = await effect();
        })();
        return () => {
            _destructor?.(result);
        };
    }, _dependencies);
};
export default useAsyncEffect;
