import { useCallback, useRef } from 'react';
import useAbortEffect from './useAbortEffect';
/**
 * it executes the effect after `ms` delay
 *
 * @returns a function to clear the timeout
 *
 * @see {@link useAbortEffect}
 *
 * @example
 * const [clearTimeoutEffect] = useTimeoutEffect(() => {
 *   console.info('this message will be printed after 1 second');
 *   return () => {
 *     console.info('this message will be printed when the effect has been cleaned up')
 *   };
 * }, 1000, []);
 *
 * <button onClick={clearTimeoutEffect}>Stop before logging</button>
 */
const useTimeoutEffect = (effect, ms, dependencies = []) => {
    const timeoutIdRef = useRef(null);
    const clearTimeoutEffect = useCallback(() => {
        if (timeoutIdRef.current) {
            clearTimeout(timeoutIdRef.current);
            timeoutIdRef.current = null;
        }
    }, []);
    useAbortEffect(signal => {
        let destructor;
        timeoutIdRef.current = setTimeout(async () => {
            destructor = await effect(signal);
        }, ms);
        return () => {
            destructor?.();
            clearTimeoutEffect();
        };
    }, [ms, ...dependencies, clearTimeoutEffect]);
    return [clearTimeoutEffect];
};
export default useTimeoutEffect;
