import useAsyncEffect from './useAsyncEffect';
/**
 * it provides an `AbortSignal` to the effect callback, the previous signal
 * will be aborted whenever the effect is cleaned up
 *
 * @see {@link useAsyncEffect}
 *
 * @example
 * useAbortEffect(signal => {
 *   setTimeout(() => {
 *     if (!signal.aborted) {
 *       // do some delayed task
 *     }
 *   }, 1000);
 *   return () => {
 *     // clean up
 *   };
 * }, [...dependencies]);
 *
 * @example
 * useAbortEffect(async signal => {
 *   const response = await fetch('https://api.example.com/resource', { signal });
 *   ...
 *   return () => {
 *     // clean up
 *   };
 * }, [...dependencies]);
 */
const useAbortEffect = (effect, dependencies) => {
    return useAsyncEffect(async () => {
        const abortController = new AbortController();
        const { signal } = abortController;
        const destructor = await effect(signal);
        return [abortController, destructor];
    }, result => {
        const [abortController, destructor] = result ?? [];
        abortController?.abort();
        destructor?.();
    }, dependencies);
};
export default useAbortEffect;
